import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { MenuService } from 'src/service/menu.service';
import { ReuseStrategyService } from 'src/service/reuse-strategy.service';
import { Menu, SystemService, Tab } from 'src/service/system.service';
import { ThemeService } from 'src/service/theme.service';

@Component({
  selector: 'app-sider',
  templateUrl: './sider.component.html',
  styleUrls: ['./sider.component.less'],
})
export class SiderComponent implements OnInit {
  @Input() isCollapsed = false;

  tabs: Tab[] = [];

  menus: Menu[] = [];

  constructor(private system: SystemService, public theme: ThemeService, private router: Router, private menu: MenuService) {
    system.menu$.subscribe((data: { tabPages: Tab[]; current: string }) => {
      this.tabs = data.tabPages;
    });
    menu.change$.subscribe((menus: Menu[]) => {
      this.menus = menus;
    });
  }

  ngOnInit(): void {}

  tab(m: Tab): void {
    const tb = this.tabs.find((t) => t.path === m.path);
    if (tb) {
      if (tb.subPage) {
        this.router.navigate([`/${environment.systemName}/${tb.path}/${tb.subPage}`]);
      } else {
        this.router.navigate([`/${environment.systemName}/${tb.path}`]);
      }
    } else {
      this.router.navigate([`/${environment.systemName}/${m.path}`]);
    }
  }
}
