import { Component, OnInit } from '@angular/core';
import { SpinnerService } from 'src/service/spinner.service';
import { Theme, ThemeService } from 'src/service/theme.service';

@Component({
  selector: 'app-theme-switch',
  templateUrl: './theme-switch.component.html',
  styleUrls: ['./theme-switch.component.less'],
})
export class ThemeSwitchComponent implements OnInit {
  currentTheme: Theme = {
    label: '',
    name: 'default',
    icon: 'bulb',
    isDark: false,
  };

  themes: Theme[] = [];

  constructor(private theme: ThemeService, private spinner: SpinnerService) {
    this.theme.theme$.subscribe((th: Theme) => {
      this.currentTheme = th;
    });

    this.themes = this.theme.themes;
  }

  change(e: string): void {
    this.theme.changeTheme(e);
  }

  ngOnInit(): void {}
}
