import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { MenuService } from 'src/service/menu.service';
import { SpinnerService } from 'src/service/spinner.service';
import { Menu, MenuMap, SystemService } from 'src/service/system.service';
import { UtilsService } from 'src/service/utils.service';

@Component({
  selector: 'app-workspace',
  templateUrl: './workspace.component.html',
  styleUrls: ['./workspace.component.less'],
})
export class WorkspaceComponent implements OnInit {
  index = 0;
  workspaces = ['dashboard', 'data', 'report', 'setting'];
  menus: MenuMap = {};

  constructor(
    private spinner: SpinnerService,
    private system: SystemService,
    private http: HttpClient,
    private menu: MenuService,
    private utils: UtilsService,
  ) {}

  ngOnInit(): void {
    this.http
      .get('assets/app-data.json')
      .toPromise()
      .then((data: any) => {
        if (data) {
          this.menus = data.menus as MenuMap;

          this.index = this.utils.getSession('index');

          this.select(this.index || 0);

          this.system.setTabs();
        }
      });
  }

  select(i: number): void {
    const space = this.workspaces[i];
    this.spinner.show('spinner', `${space}...`);
    this.index = i;
    this.utils.setSession('index', this.index);
    const ms = this.menus[space];
    this.menu.setMenu(ms);
    setTimeout(() => {
      this.spinner.hidde('spinner');
    }, 1000);
  }
}
