import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NavService } from 'src/service/nav.service';
import { ReuseStrategyService } from 'src/service/reuse-strategy.service';
import * as _ from 'lodash';

@Component({
  selector: 'app-breadcrumb',
  templateUrl: './breadcrumb.component.html',
  styleUrls: ['./breadcrumb.component.less'],
})
export class BreadcrumbComponent implements OnInit {
  constructor(private location: Location, private router: Router, private nav: NavService) {}

  ngOnInit(): void {}

  onBack(): void {
    this.location.back();
  }

  refresh(): void {
    const url = this.nav.getUrl(this.router.url);
    url.param.timestamp = new Date().getTime();
    ReuseStrategyService.deleteRouteSnapshot(url.path);
    _.remove(this.nav.history, _.first(this.nav.history));
    this.router.navigate([url.path, url.param]);
  }
}
