import { registerLocaleData } from '@angular/common';
import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import zh from '@angular/common/locales/zh';
import en from '@angular/common/locales/en';

export interface Lang {
  lang: string;
  label: string;
  abbr: string;
}

@Injectable({
  providedIn: 'root',
})
export class LangService {
  private langList: Lang[] = [
    { lang: 'zh-CN', label: '', abbr: '' },
    { lang: 'en-US', label: 'English', abbr: '' },
  ];

  private defaultLang: Lang = { lang: 'zh-CN', label: '', abbr: '' };

  private langChange: Subject<Lang> = new Subject<Lang>();

  constructor() {}

  get langs(): Lang[] {
    return this.langList;
  }

  get lang$(): Observable<Lang> {
    return this.langChange.asObservable();
  }

  change(langCd: string): void {
    const lang = this.langList.find((l) => l.lang === langCd);
    if (lang) {
      registerLocaleData(langCd === 'zh-CN' ? zh : en);
      this.langChange.next(lang);
      return;
    }

    registerLocaleData(zh);
    this.langChange.next(this.defaultLang);
  }
}
