import { Component, OnInit } from '@angular/core';
import { Lang, LangService } from 'src/service/lang.service';
import { SpinnerService } from 'src/service/spinner.service';

@Component({
  selector: 'app-lang-switch',
  templateUrl: './lang-switch.component.html',
  styleUrls: ['./lang-switch.component.less'],
})
export class LangSwitchComponent implements OnInit {
  currentLang: Lang = { lang: 'zh-CN', label: '', abbr: '' };

  langList: Lang[] = [];

  constructor(private lang: LangService, private spinner: SpinnerService) {
    this.lang.lang$.subscribe((l: Lang) => {
      this.currentLang = l;
    });

    this.langList = this.lang.langs;
  }

  ngOnInit(): void {}

  change(lang: string): void {
    this.lang.change(lang);
  }
}
