import {
  AfterViewInit,
  Directive,
  ElementRef,
  Input,
  OnChanges,
  Renderer2,
  SimpleChanges,
} from '@angular/core';

@Directive({
  selector: '[appActive]',
})
export class ActiveDirective implements AfterViewInit, OnChanges {
  @Input() index = 0;

  constructor(private render2: Renderer2, private el: ElementRef) {}

  ngAfterViewInit(): void {
    const container: HTMLElement = this.el.nativeElement;
    const items = container.getElementsByClassName('item');
    for (let i = 0; i < items.length; i++) {
      const e = items.item(i);
      this.render2.removeClass(e, 'active');
    }

    const element = items.item(this.index);
    this.render2.addClass(element, 'active');
  }

  ngOnChanges(changes: SimpleChanges): void {
    const container: HTMLElement = this.el.nativeElement;
    const items = container.getElementsByClassName('item');
    for (let i = 0; i < items.length; i++) {
      const e = items.item(i);
      this.render2.removeClass(e, 'active');
    }

    const element = items.item(this.index);
    this.render2.addClass(element, 'active');
  }
}
