import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { remove } from 'lodash';
import { environment } from 'src/environments/environment';
import { ReuseStrategyService } from 'src/service/reuse-strategy.service';
import { SystemService, Tab } from 'src/service/system.service';

@Component({
  selector: 'app-tabs',
  templateUrl: './tabs.component.html',
  styleUrls: ['./tabs.component.less'],
})
export class TabsComponent implements OnInit {
  tabs: Tab[] = [];
  selectedIndex = 0;

  show = false;
  constructor(private router: Router, private system: SystemService) {
    system.menu$.subscribe((data: { tabPages: Tab[]; current: string }) => {
      this.tabs = data.tabPages;
      setTimeout(() => {
        this.selectedIndex = data.tabPages.findIndex((x) => x.path === data.current);
      }, 0);
    });
  }

  ngOnInit(): void {}

  get showClose(): boolean {
    return this.tabs && this.tabs.length > 1;
  }

  closeTab(tab: Tab): void {
    let index = 0;
    if (tab) {
      // tab
      this.tabs = remove(this.tabs, (m, i) => {
        if (m.path === tab.path) {
          index = i;
        }
        return m.path !== tab.path;
      });
      // tab
      ReuseStrategyService.deleteRouteSnapshot(`/${environment.systemName}/${tab?.router}`);
    }
    // sessiontab
    this.system.session.tabPages = [...this.tabs];

    // 
    // 
    if (tab?.path === this.system.session.activatedPage) {
      let pushIndex = null;
      if (index === 0 && this.tabs.length >= 1) {
        pushIndex = 0;
      }
      if (index > 0 && this.tabs.length > index) {
        pushIndex = index;
      }
      if (index > 0 && this.tabs.length <= index) {
        pushIndex = index - 1;
      }
      if (pushIndex !== null) {
        this.navigate(pushIndex);
      }
    }
  }

  /**
   * tab
   * @param index tab
   */
  navigate(index: number): void {
    this.selectedIndex = index;
    const tab = this.tabs.find((x, i) => i === index);
    if (tab && tab.path) {
      let page = tab.path;
      const subPage = tab.subPage;
      const param = tab.param || {};
      if (subPage) {
        page += `/${subPage}`;
      }
      this.router.navigate([`/${environment.systemName}/${page}`, param]);
    }
  }
}
